import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	components: {
		uniPopup,
	},
	props: {
		cardinfo: {
			type: Array,
		},
		isShowTip: {
			type: Boolean,
		},
		indexId: {
			type: Number,
		},
		defaultId: {
			type: Number,
		},
	},
	data() {
		return {
			// indexId: 0,
			show: false,
			type: 'bottom',
		}
	},
	methods: {
		togglePopup() {
			this.$refs['share'].open()
		},
		cancel() {
			this.$refs['share'].close()
		},
		change(index) {
			this.indexId = index
			this.$refs['share'].close()
		},
		toPage(url) {
			uni.navigateTo({
				url: '../../pages/' + url,
			});
		},
	},
}
